.camera-container{
    position: relative;
    width: 100%;
    height: inherit;
}

.camera-container video {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100%;
    height: inherit;
}

.camera-container img {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100%;
    height: inherit;
}

.camera-container button {
    position: absolute;
    display: block;
    box-sizing: border-box;
}